import { Component, OnInit, ViewChild } from '@angular/core';
import { Router,NavigationExtras } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, App_loginApi, ProjectsApi, Project_photosApi } from '../../../../shared/loopback-sdk';
import { ActivatedRoute } from "@angular/router";
import { Headers, Http, Response } from '@angular/http';
import * as moment from 'moment'; //for date manupalation
import {ConfirmationService} from 'primeng/primeng';
import { Lightbox } from 'angular2-lightbox';
import {PaginatorModule} from 'primeng/primeng';
import {Message,MenuItem,SelectItem} from 'primeng/primeng';
import { CookieService } from 'angular2-cookie/core';
declare var window:any;
declare var $;
declare var sdkPrefix;
declare var hostname:any;
@Component({
	selector: "view Project",
	templateUrl: 'view-project.component.html',
	providers: [ConfirmationService],
})

export class ViewProjectComponent {
	result: any;
	success: boolean;
	display: boolean;
	public id: number;
	public role: string;
	submited:any=false;
	_albums:any = [];
	projectData: any = [];
	backLink:any = "";
	busy:any;
	emptyMessage:String="No Result Found";
	displayConfirm: boolean = false;
	msgs: Message[] = [];
	maxDate:Date;
	loading:boolean = true;
	chapterName:any;
	chapterStatus:any="";
	createdAt:any;
	chapterNameSearch:any;
	chapterStatusSearch:any="";
	createdAtSearch:any;
	paginationVal:any=false;
	loginFlagToken:any;
	perPageLimit:Number = 10;
	offset:Number=0;
	total:number;
	chapterData:any=[];
	ifEmpty:boolean=false;
	projectConstructionStagging = [];
	projectTypeHousingArr = [];
	workGroupArr = [];
	showWrkGroup = true;

	constructor(private router:Router,private http:Http, private projectApi:ProjectsApi, private photosApi:Project_photosApi, private _cookieService: CookieService, private _lightbox: Lightbox, private confirmationService: ConfirmationService, private appLoginApi:App_loginApi,private loopAuth:LoopBackAuth,private route: ActivatedRoute) {
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		console.log('this is called');
		let dataObj={};
		let uid=this.id;
		let dataArrObj={
			"project_id":this.id
		}

		this.loading=true;
		this.projectApi.getProjects(dataArrObj).subscribe(
			projectData=>{
				var projectResult = projectData.result[0];
				var data = {
					result:[ projectResult.project_details ]
				}
				this._albums = [];
				if(data.result.length>0){
					if(data.result[0].status == 1){
						data.result[0].status = 'Active';
					}else{
						data.result[0].status = 'Inactive';
					}
					if(data.result[0].created_date){
						data.result[0].created_date = moment(data.result[0].created_date).format("DD - MMM - YYYY");
					}else{
						data.result[0].created_date = "";
					}
					if(data.result[0].updated_date){
						data.result[0].updated_date = moment(data.result[0].updated_date).format("DD - MMM - YYYY");
					}else{
						data.result[0].updated_date = ""
					}
					if(data.result[0].project_start_date){
						data.result[0].project_start_date = moment(data.result[0].project_start_date).format("DD - MMM - YYYY");
					}
					if(data.result[0].project_completion_date){
						data.result[0].project_completion_date = moment(data.result[0].project_completion_date).format("DD - MMM - YYYY");
					}
					if(data.result[0].project_cover_photo){
						let img = data.result[0].project_cover_photo;
						data.result[0].project_cover_photo = hostname+"/api/containers/projectImage/download/"+img+'?access_token='+this.loginFlagToken;
						this._albums.push(
							{
								src:data.result[0].project_cover_photo,
								thumb:data.result[0].project_cover_photo
							}
						);
					}
					this.projectData = data.result[0];
					this.getProjectPhotos();
				}

				if(projectResult.construction_stage.length > 0 ){
					for (const item in projectResult.construction_stage) {
						this.projectConstructionStagging.push(projectResult.construction_stage[item]);
					}
				}
				if(projectResult.typeOfHousingObj.length > 0 ){
					for (const key in projectResult.typeOfHousingObj) {
						this.projectTypeHousingArr.push(projectResult.typeOfHousingObj[key]);
					}
				}
				if(projectResult.work_group.length > 0 ){
					this.workGroupArr = [];
					let wrkGrpLength = projectResult.work_group.length;
					let wrkGrpEmptyCount = 0;
					for (const key in projectResult.work_group) {
						
						let wrkGrpType = projectResult.work_group[key].member_type;
							let wrkGropNamesArr = JSON.parse(projectResult.work_group[key].member_name);
							let wrkGroupNamesArr = [];
							if(wrkGropNamesArr.length == 0){
								wrkGrpEmptyCount++;
							}
							for(let i = 0;i<5;i++){
								
								if(wrkGropNamesArr[i] != undefined && wrkGropNamesArr[i] != null && wrkGropNamesArr[i] != ''){
									wrkGroupNamesArr.push(wrkGropNamesArr[i]);
								}else{
									wrkGroupNamesArr.push({'name':'-'});
								}
							}
							
							let wrkGrpObj = {
								memter_type : wrkGrpType,
								members_name : wrkGroupNamesArr
							}
							this.workGroupArr.push(wrkGrpObj);
					}
					console.log("count :",wrkGrpEmptyCount ,"length :",wrkGrpLength);
							if(wrkGrpLength == wrkGrpEmptyCount){
								this.showWrkGroup = false;
							}
					
				}

			},
			err=>{
				this.loading=false;
			},
			()=>{
				this.loading=false;
			}
		);
	}

	getProjectPhotos(){
		let cond = {"where":{"project_id":this.id,"status":1}};
		let api="/api/project_photos?filter=";
		let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+this.loginFlagToken;

		//this.photosApi.find({where:{and: [{"project_id":this.id}, {"status":1}]}})
		this.http.get(url).subscribe(
			(result)=>{
				this.loading=false;
				let data = result.json();
				if(data.length > 0){
					for(var i=0; i<data.length; i++){
						console.log("Data",[i]," : ",data[i]);
						if(data[i]['project_photo_source']){
							let img = data[i]['project_photo_source'];
							img = "http://phapi.experiencecommerce.com:4500/api/containers/projectImage/download/"+img+'?access_token='+this.loginFlagToken;
							this._albums.push(
								{
									src:img,
									thumb:img
								}
							);
						}
					}

					console.log("albums",this._albums);

				}
			},
			err=>{
				this.loading=false;
			},
			()=>{
				this.loading=false;
			}
		);
	}

	open(index: number): void {
		// open lightbox
		this._lightbox.open(this._albums, index);
	}
}


